const width = document.documentElement.clientWidth;
const body = document.querySelector('.body');

const burger = document.querySelector('.burger');
const burgerFooter = document.querySelector('.burger--footer');
const burgerMenu =  document.querySelector('.burger__menu');
const burgerClose = document.querySelector('.burger__menu-close');
burger.addEventListener('click', (e) => {
  burger.classList.toggle('burger--active');
  body.classList.toggle('body--no-scroll');
  burgerMenu.classList.toggle('burger__menu--active');
});
burgerFooter.addEventListener('click', (e) => {
  e.currentTarget.classList.toggle('burger--active');
});
burgerClose.addEventListener('click', () => {
  burger.classList.toggle('burger--active');
  body.classList.toggle('body--no-scroll');
  burgerMenu.classList.toggle('burger__menu--active');
})

$('.gallery__list').slick({
  infinite: true,
  slidesToShow: 3,
  slidesToScroll: 1,
  nextArrow: '.gallery__arrow--right',
  prevArrow: '.gallery__arrow--left',
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 2
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1
      }
    }
  ]
});

const offersList = document.querySelectorAll('.offers__items');

let index = 0;
if (width<576){
  offersList.forEach(elem => {
    if (index>4 && index!=(offersList.length - 1)){
      elem.classList.add('offers__items--no-active')
    }
    index++;
  });
}
